import './abstract-view.css';
import View from './view';
export default class ShakeView extends View {
    constructor() {
        super();
    }
    shake(callback) {
        this.element.classList.add("shake" /* SnakeAnimation.CLASS_NAME */);
        setTimeout(() => {
            this.element.classList.remove("shake" /* SnakeAnimation.CLASS_NAME */);
            callback?.();
        }, 600 /* SnakeAnimation.ANIMATION_TIMEOUT */);
    }
}
